export default {
    data() {
        return {
            insta1: '',
            insta2: '',
            insta3: '',
        }
    },
    created() {
        this.$axios.$get('/api/insta/getAll').then((data) => {
            this.insta1 = data[0].link;
            this.insta2 = data[1].link;
            this.insta3 = data[2].link;
        })
    }
}
